.backgroundImage {
  filter: brightness(150%);
  height: 500px;
}

.posterImage {
  float: left;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  object-fit: cover;
  width: 200px;
  transition: 0.5s;
}

.posterImage:hover {
  width: 220px;
  transition: 0.5s;
}

.detail {
  display: flex;
  flex-flow: row nowrap;
  position: relative;
  top: -100px;
  left: 40px;
  width: 90%;
}

.detail p {
  display: -ms-flexbox;
  display: -moz-box;
  align-self: center;
  margin: 2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-all;
  word-spacing: 3px;
  letter-spacing: 1px;
  font-size: 20px;
  max-height: 108px;
}

.background {
  display: flex;
  flex-flow: column;
  height: 100vh;
}
